/* Line highlight plugin */
pre[data-line] {
	position: relative;
	padding: 1em 0 1em 3em;
}

.line-highlight {
	position: absolute;
	left: 0;
	right: 0;
	margin-top: 1em; /* Same as .prism's padding-top */
	background: rgba(255, 255, 255, .2);
	pointer-events: none;
	line-height: inherit;
	white-space: pre;
}

.line-highlight:before,
.line-highlight[data-end]:after {
	content: attr(data-start);
	position: absolute;
	top: .3em;
	left: .6em;
	min-width: 1em;
	padding: 0 .5em;
	background-color: rgba(255, 255, 255, .3);
	color: #fff;
	font: bold 65%/1.5 sans-serif;
	text-align: center;
	border-radius: 8px;
	text-shadow: none;
}

.line-highlight[data-end]:after {
	content: attr(data-end);
	top: auto;
	bottom: .4em;
}

/* for line numbers */
